import { Component } from '@angular/core';
import { ActivatedRoute } from '@angular/router';
import { Observable, merge, ObservedValuesFromArray } from 'rxjs';
import { DrinkSessionService } from '../drink-session.service';
import { DrinkCard } from '../models/drink-card';

@Component({
  selector: 'app-tab1',
  templateUrl: 'tab1.page.html',
  styleUrls: ['tab1.page.scss']
})
export class Tab1Page {

  drinkCardList: DrinkCard[] = [];

  constructor(
    private route: ActivatedRoute,
    private drinkSessionService: DrinkSessionService
  ) { }

  ngOnInit() {
    this.drinkCardList = JSON.parse(this.route.snapshot.paramMap.get('drinkList')) || [];
    console.log(this.drinkCardList);
  }

  saveDrinkList() {
    this.drinkSessionService.saveCurrentDrinkList(this.drinkCardList);
  }

  addDrinkCard() {
    this.drinkCardList.push({
      name: 'Cerveza',
      price: 1.8,
      count: 0
    })
  }

}
