import { Component, Input, OnInit, Output, EventEmitter } from '@angular/core';

@Component({
  selector: 'drink-counter-card',
  templateUrl: './drink-counter-card.component.html',
  styleUrls: ['./drink-counter-card.component.scss'],
})
export class DrinkCounterCardComponent implements OnInit {

  constructor() { }

  ngOnInit() { }

  @Input()
  count = 0;
  @Output()
  countChange = new EventEmitter<number>();
  @Input()
  price: number;
  @Input()
  name = '';

  @Output()
  onChange = new EventEmitter<null>();

  addDrink() {
    this.count++;
    this.countChange.emit(this.count);
    this.onChange.emit();
  }

  removeDrink() {
    if (this.count > 0) {
      this.count--;
      this.countChange.emit(this.count);
      this.onChange.emit();
    }
  }

}
